.side_bar {
  background-color: rgb(15, 15, 15);
  color: white;
  text-align: left;
  position: fixed;
  width: 300px;
  height: 100%;
  top: 0;
  right: -300px;
  z-index: 1;
  transition: right 0.35s ease;
}

.empty_space {
  display: flex;
  width: 100%;
  margin-bottom: 50px;
}

.sideBar_el {
  width: 100%;
  margin-left: 10px;
  color: white;
  font-size: 20px;
  cursor: pointer;
}

.sideBar_hr {
  border: solid 1px white;
  margin-bottom: 15px;
}
/*-------------------------------hamburger button--------------------------------*/
.burger {
  position: fixed;
  width: 25px;
  height: 20px;
  background: transparent;
  cursor: pointer;
  display: block;
  margin-top: 7px;
  margin-right: 8px;
  right: 5px;
  z-index: 2;
}

.burger input {
  display: none;
}

.burger span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: white;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

.burger span:nth-of-type(1) {
  top: 0px;
  transform-origin: left center;
}

.burger span:nth-of-type(2) {
  top: 50%;
  transform: translateY(-50%);
  transform-origin: left center;
}

.burger span:nth-of-type(3) {
  top: 100%;
  transform-origin: left center;
  transform: translateY(-100%);
}

.burger input:checked ~ span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 0px;
  left: 5px;
}

.burger input:checked ~ span:nth-of-type(2) {
  width: 0%;
  opacity: 0;
}

.burger input:checked ~ span:nth-of-type(3) {
  transform: rotate(-45deg);
  top: 17.5px;
  left: 5px;
}
/*-------------------------------hamburger button--------------------------------*/

